/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"about-page\">\n    <section class=\"about about__top\">\n        <div class=\"about__top-photo \" data-animate=\"\">\n            <img src=\"https://almette.ru/images/about/02_about_hohland_1.jpg\" class=\"image_cover mobile__hide\" alt=\"\">\n            <img src=\"https://almette.ru/images/mobile/about/02_about_hohland_1_mob.jpg\" class=\"image_cover desktop__hide\" alt=\"\">\n        </div>\n        <div class=\"wrapper padding-left_zero\">\n            <div class=\"box box-35 box_blue margin-top__minus_225\" appAnimateTranslate=\"speed_slow\">\n                <article class=\"pos-top pos-top_35 width-400\">\n                    <h1 class=\"\">\n                        О компании <br>\n                        «Хохланд»\n                    </h1>\n                    <h5>\n                        Творожный сыр «Альметте» производится немецкой семейной компанией «Хохланд».\n                        В 2017 году «Хохланд» отпраздновал свое 90‑летие!\n                    </h5>\n                </article>\n            </div>\n        </div>\n    </section>\n    <section class=\"about about__video margin-top__minus_90\">\n        <div class=\"wrapper padding-right_zero\">\n            <div class=\"b-flex b-flex_horizontal_end about__video-content\">\n                <div class=\"video\" appAnimateTranslate=\"speed_middle\">\n                    <div class=\"video__play\" appVideoPopup=\"almette\">\n                        <svg class=\"play__svg\">\n                            <use xlink:href=\"/assets/img/svg-sprite/svg.svg#ico_play\"></use>\n                        </svg>\n                    </div>\n                </div>\n            </div>\n\n            <article class=\"width-per_75 about__video-text\">\n                <h5>\n                    Главные ценности «Хохланд руссланд»: соблюдение традиций, <br>\n                    преемственность опыта, ответственное\n                    отношение к работе, высокое <br>\n                    качество продукции и доверие потребителей.\n                </h5>\n                <p>\n                    Миссия «Хохланд Руссланд»: день за днем делать все возможное, чтобы вдохновлять людей своей\n                    продукцией. Во\n                    <br>\n                    всем, что делает «Хохланд Руссланд», компания берет на себя ответственность за людей и окружающую\n                    среду.\n                    <br>\n                    «Хохланд Руссланд» заботится о наших детях и внуках.\n                </p>\n            </article>\n        </div>\n    </section>\n    <section class=\"about about__history\">\n        <div class=\"wrapper about__history-title\">\n            <article appAnimateTranslate=\"speed_middle\">\n                <h3>\n                    Как начиналась <br>\n                    история «Хохланд»\n                </h3>\n                <h5>\n                    1 сентября 1927 предприниматель Роберт Райх вместе со своим шурином <br> Георгом Зуммером\n                    основали в\n                    альпийском местечке Госхольц (пригород <br> Линденберга, Германия) завод по производству\n                    плавленых\n                    сыров «Хохланд».\n                </h5>\n                <p>\n                    Вскоре у продукции «Хохланд» появились постоянные покупатели в больших городах – Берлине,\n                    Дрездене <br> и Лейпциге.\n                </p>\n            </article>\n        </div>\n        <div class=\"wrapper padding-left_zero padding-right_zero\">\n            <div class=\"about__history-photo\" appAnimateTranslate=\"speed_slow\">\n                <img src=\"https://almette.ru/images/about/02_about_hohland_2.jpg\" class=\"image_cover mobile__hide\" alt=\"\">\n                <img src=\"https://almette.ru/images/mobile/about/02_about_hohland_2_mob.jpg\" class=\"image_cover desktop__hide\" alt=\"\">\n            </div>\n            <div class=\"b-flex b-flex_horizontal_end margin-top__minus_155\">\n                <div class=\"box box_white box-49\" appAnimateTranslate=\"speed_middle\">\n                    <article>\n                        <h5>\n                            В 1931 году Райх и Зуммер выкупили здание закрывшейся фабрики по производству шляп, чтобы\n                            открыть там сыроварню и обустроить квартиры для своих рабочих.\n                        </h5>\n                        <p>\n                            Предприятие с 30 сотрудниками переехало в соседний поселок Хайменкирх. Через несколько лет\n                            «Хохланд» стали самым крупным производителем блочного сыра в Германии.\n                        </p>\n                    </article>\n                </div>\n            </div>\n        </div>\n    </section>\n    <section class=\"about about__dates\">\n        <div class=\"wrapper\">\n            <article appAnimateTranslate=\"speed_slow\">\n                <h3>\n                    Развитие компании <br>\n                    «Хохланд»\n                </h3>\n                <h5>\n                    После Второй мировой войны у руля торгового представительства <br>\n                    «Хохланд» встал сын Георга Зуммера –\n                    Роберт. Под его руководством <br> компания стала осваивать новые рынки.\n                </h5>\n            </article>\n            <div class=\"about__dates-item b-flex_horizontal_end\">\n                <h2 class=\"date_left\" appAnimateTranslate=\"speed_middle\">\n                    1955\n                </h2>\n                <h4 class=\"width_650\" appAnimateTranslate=\"speed_slow\">\n                    «Хохланд» внимательно следили за изменением предпочтений покупателей: в 1955 компания начала\n                    выпускать сыр в нарезке и порциями.\n                </h4>\n            </div>\n            <div class=\"about__dates-item \">\n                <h4 class=\"width_580\" appAnimateTranslate=\"speed_slow\">\n                    В 1960 в состав предприятия вошел завод <br>\n                    по производству сыра Эмменталь.\n                </h4>\n                <h2 class=\"date_right\" appAnimateTranslate=\"speed_middle\">\n                    1960\n                </h2>\n            </div>\n            <div class=\"about__dates-item b-flex_horizontal_end\">\n                <h2 class=\"date_left\" appAnimateTranslate=\"speed_middle\">\n                    1962\n                </h2>\n                <h4 class=\"width_580\" appAnimateTranslate=\"speed_slow\">\n                    К 1962 году производство плавленого сыра «Хохланд» стало крупнейшим в Европе, а в середине 60-х\n                    начались продажи за ее пределами.\n                </h4>\n            </div>\n            <div class=\"about__dates-item \">\n                <h4 class=\"width_620\"  appAnimateTranslate=\"speed_slow\">\n                    В начале 70-х «МакДоналдс» открыл свои рестораны в Германии, где выбрал в качестве поставщика сыра\n                    компанию «Хохланд».\n                </h4>\n                <h2 class=\"date_right\" appAnimateTranslate=\"speed_middle\">\n                    1970\n                </h2>\n            </div>\n            <div class=\"about__dates-item b-flex_horizontal_end\">\n                <h2 class=\"date_left\" appAnimateTranslate=\"speed_middle\">\n                    1988\n                </h2>\n                <h4 class=\"width_650\" appAnimateTranslate=\"speed_slow\">\n                    В 1988 году «Хохланд» купили у «Первого баварского маслодельного завода» сыроварни в баварском\n                    городе в Шонгау.\n                </h4>\n            </div>\n            <div class=\"about__dates-item \">\n                <h4 class=\"width_620\" appAnimateTranslate=\"speed_slow\">\n                    В 1997 году в Шонгау открылось производство творожного сыра «Альметте».\n                </h4>\n                <h2 class=\"date_right\" appAnimateTranslate=\"speed_middle\">\n                    1997\n                </h2>\n            </div>\n            <div class=\"about__dates-item b-flex_horizontal_end\">\n                <h2 class=\"date_left\" appAnimateTranslate=\"speed_middle\">\n                    2000\n                </h2>\n                <h4 class=\"width_620\" appAnimateTranslate=\"speed_slow\">\n                    В 2000 году «Хохланд» запустили собственное производство в России и заняли позицию лидера на рынке.\n                </h4>\n            </div>\n        </div>\n    </section>\n    <section class=\"about about__country\">\n        <div class=\"wrapper\">\n            <article>\n                <h3>\n                    «Хохланд» <br>\n                    в&nbsp;России\n                </h3>\n                <div class=\"b-flex_between\">\n                    <h5 class=\"width_860\">\n                        В&nbsp;России располагаются два высокотехнологичных завода «Хохланд Руссланд»,\n                        на&nbsp;которых производятся три бренда сыров:\n                    </h5>\n                    <div class=\"small-box__line width_305 mobile__hide\">\n                        <article>\n                            <p>\n                                Сегодня «Хохланд Руссланд»&nbsp;— лидер среди производителей сыров\n                                в&nbsp;России в&nbsp;обоих сегментах.\n                            </p>\n                        </article>\n                    </div>\n                </div>\n                <div class=\"width width_860\">\n                    <div class=\"list list__circle\">\n                        <article>\n                            <ul>\n                                <li>\n                                    <p>\n                                        «Альметте»&nbsp;— творожный сыр;\n                                    </p>\n                                </li>\n                                <li>\n                                    <p>\n                                        «Хохланд»&nbsp;— плавленые и&nbsp;творожные сыры;\n                                    </p>\n                                </li>\n                                <li>\n                                    <p>\n                                        «Фетакса»&nbsp;— сыр фета.\n                                    </p>\n                                </li>\n                            </ul>\n                            <p>\n                                Помимо розничной торговли компания также плотно работает с&nbsp;сегментом\n                                общественного питания.\n                            </p>\n                        </article>\n                    </div>\n                    <div class=\"small-box__line width_305 desktop__hide\">\n                        <article>\n                            <p>\n                                «Хохланд Руссланд»&nbsp;— лидер среди производителей сыров в&nbsp;России.\n                            </p>\n                        </article>\n                    </div>\n                </div>\n            </article>\n        </div>\n    </section>\n    <section class=\"about about__country-photo\">\n        <div class=\"wrapper padding-left_zero\">\n            <div class=\"about__history-photo\" appAnimateTranslate=\"speed_slow\">\n                <img src=\"https://almette.ru/images/about/02_about_hohland_3.jpg\" class=\"image_cover mobile__hide\" alt=\"\">\n                <img src=\"https://almette.ru/images/mobile/about/02_about_hohland_3_mob.jpg\" class=\"image_cover desktop__hide\" alt=\"\">\n            </div>\n        </div>\n    </section>\n    <section class=\"about about__country-content\">\n        <div class=\"wrapper padding-right_zero \">\n            <div class=\"b-flex b-flex_horizontal_end\">\n                <div class=\"box box_white box-49 b-flex_vertical\" appAnimateTranslate=\"speed_middle\">\n                    <article>\n                        <h5>\n                            Компания также производит сыры для&nbsp;профессионального применения под маркой «Хохланд\n                            Профешнл».\n                        </h5>\n                        <p>\n                            Российские заводы соответствуют международными схемам сертификации\n                            пищевой отрасли\n                            IFS и&nbsp;SQMS, а&nbsp;также оснащены собственными лабораториями, благодаря которым\n                            микробиологический контроль качества происходит на&nbsp;всех этапах производства: от&nbsp;входного\n                            контроля сырья до&nbsp;готовой продукции.\n                        </p>\n                    </article>\n                </div>\n            </div>\n        </div>\n    </section>\n</div>");

