/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"geography-page overlay\" [class.overlay_show]=\"currentName !== 0\">\n    <div class=\"geography-page__bg\">\n        <img src=\"/assets/img/geography/map_new.jpg\" alt=\"\">\n    </div>\n    <div class=\"wrapper__big\">\n        <div class=\"geography__top\">\n            <article class=\"geography__left geography__top-item\">\n                <h1>\n                    География <br>\n                    производства\n                </h1>\n                <h5 class=\"geography__top_margin\">\n                    Рецепт «Альметте» был рожден в&nbsp;местечке Шонгау, у&nbsp;подножья баварских Альп.\n                    В&nbsp;этом регионе столетиями поддерживается традиция фермерства и&nbsp;животноводства.\n                    Альпийские луга, чистый воздух и&nbsp;прозрачные воды горных рек&nbsp;— что еще нужно для\n                    производства молока высокого качества? Не&nbsp;удивительно, что именно здесь производство\n                    молодых сыров стало традиционным.\n                </h5>\n            </article>\n            <div class=\"geography__right geography__top-item\">\n                <div class=\"geography__right__item geography__right__item-one\">\n                    <h2 class=\"\">\n                        500 000\n                    </h2>\n                    <h3 class=\"text_right\">\n                        тонн\n                    </h3>\n                    <div class=\"b-flex_horizontal_end geography_content-one\">\n                        <div class=\"box geography__box box_white b-flex_vertical padding-bottom_zero pos-top\">\n                            <article class=\"width_205\">\n                                <p>\n                                    За&nbsp;год в&nbsp;Белгородской области производят более 500&nbsp;тыс. тонн молока.\n                                </p>\n                            </article>\n                        </div>\n                    </div>\n                </div>\n                <div class=\"geography__right__item geography__right__item-two\">\n                    <div class=\"b-flex_horizontal_end\">\n                        <div class=\"box geography__box box_white b-flex_vertical padding-bottom_zero\">\n                            <section class=\"margin-left__minus_80\">\n                                <h2 class=\"\">\n                                    2,5\n                                </h2>\n                                <h3 class=\"\">\n                                    стакана\n                                </h3>\n                            </section>\n                            <article class=\"width_195\">\n                                <p>\n                                    Для приготовления <br>\n                                    1&nbsp;бочонка творожного<br>\n                                    сыра Almette необходимо<br>\n                                    2,5 стакана свежего <br>молока.\n                                </p>\n                            </article>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n    <div [class.active]=\"currentName === 1\" class=\"geography__pin-item geography__pin-one\">\n        <div class=\"pin__wrapper\">\n            <div class=\"pin-illustration illustration__one\">\n                <svg class=\"icon__svg\">\n                    <use xlink:href=\"/assets/img/svg-sprite/svg.svg#schongau\"></use>\n                </svg>\n            </div>\n            <div class=\"pin\" (click)=\"geographyOpenPopup(1)\">\n                <svg class=\"icon__svg\">\n                    <use xlink:href=\"/assets/img/svg-sprite/svg.svg#pin\"></use>\n                </svg>\n            </div>\n            <h3>\n                Шонгау, <br>\n                Германия\n            </h3>\n        </div>\n\n        <div class=\"geography__popup\">\n            <div class=\"box box_white box__geography\">\n                <article class=\"pos-top pos-top_35\">\n                    <h2 class=\"\">\n                        Шонгау, <br>\n                        Германия\n                    </h2>\n                    <p>\n                        Чтобы сохранить оригинальными консистенцию и&nbsp;вкус «Альметте», места для\n                        строительства заводов в&nbsp;других странах выбирались с&nbsp;такими&nbsp;же требованиями,\n                        как и&nbsp;к&nbsp;первому предприятию в&nbsp;Шонгау: это должен быть экологически чистый\n                        район с&nbsp;развитым производством молока высокого качества.\n                    </p>\n                </article>\n                <div class=\"close\" (click)=\"geographyPopupClose($event)\">\n                    <svg class=\"close-svg\">\n                        <use xlink:href=\"/assets/img/svg-sprite/svg.svg#ico_close_d\"></use>\n                    </svg>\n                </div>\n            </div>\n            <div class=\"b-flex_horizontal_end\">\n                <div class=\"geography__photo\">\n                    <img src=\"https://almette.ru/images/geography/06_geography_schongau.jpg\" class=\"image_cover mobile__hide\" alt=\"\">\n                    <img src=\"https://almette.ru/images/mobile/geography/06_geography_schongau_mob.jpg\" class=\"image_cover desktop__hide\" alt=\"\">\n                </div>\n            </div>\n        </div>\n    </div>\n    <div [class.active]=\"currentName === 2\" class=\"geography__pin-item geography__pin-two\">\n        <div class=\"pin__wrapper\">\n            <div class=\"pin-illustration illustration__two\">\n                <svg class=\"icon__svg\">\n                    <use xlink:href=\"/assets/img/svg-sprite/svg.svg#wengrow\"></use>\n                </svg>\n            </div>\n            <div class=\"pin\" (click)=\"geographyOpenPopup(2)\">\n                <svg class=\"icon__svg\">\n                    <use xlink:href=\"/assets/img/svg-sprite/svg.svg#pin\"></use>\n                </svg>\n            </div>\n            <h3>\n                Венгрув, <br>\n                Польша\n            </h3>\n        </div>\n        <div class=\"geography__popup\">\n            <div class=\"box box_white box__geography\">\n                <article class=\"pos-top pos-top_35\">\n                    <h2 class=\"\">\n                        Венгрув, <br>\n                        Польша\n                    </h2>\n                    <p>\n                        Так, второй завод по&nbsp;производству творожного сыра «Альметте» основали в&nbsp;городке\n                        Венгрув на&nbsp;востоке Польши. Венгрувская низменность расположена в&nbsp;регионе, который\n                        называют «Зелеными легкими Польши». Он&nbsp;объединяет самые экологически чистые\n                        районы страны и&nbsp;располагает всеми условиями для получения первоклассного молока.\n                    </p>\n                </article>\n                <div class=\"close\" (click)=\"geographyPopupClose()\">\n                    <svg class=\"close-svg\">\n                        <use xlink:href=\"/assets/img/svg-sprite/svg.svg#ico_close_d\"></use>\n                    </svg>\n                </div>\n            </div>\n            <div class=\"b-flex_horizontal_end\">\n                <div class=\"geography__photo\">\n                    <img src=\"https://almette.ru/images/geography/06_geography_wengrow.jpg\" class=\"image_cover mobile__hide\" alt=\"\">\n                    <img src=\"https://almette.ru/images/mobile/geography/06_geography_wengrow_mob.jpg\" class=\"image_cover desktop__hide\" alt=\"\">\n                </div>\n            </div>\n        </div>\n    </div>\n    <div [class.active]=\"currentName === 3\" class=\"geography__pin-item geography__pin-three\">\n        <div class=\"pin__wrapper\">\n            <div class=\"pin-illustration illustration__three\">\n                <svg class=\"icon__svg\">\n                    <use xlink:href=\"/assets/img/svg-sprite/svg.svg#prokhorovka\"></use>\n                </svg>\n            </div>\n            <div class=\"pin\" (click)=\"geographyOpenPopup(3)\" >\n                <svg class=\"icon__svg\">\n                    <use xlink:href=\"/assets/img/svg-sprite/svg.svg#pin\"></use>\n                </svg>\n            </div>\n            <h3>\n                Прохоровка,\n            </h3>\n            <h3>\n                Россия\n            </h3>\n        </div>\n\n        <div class=\"geography__popup\">\n            <div class=\"box box_white box__geography\">\n                <article class=\"pos-top pos-top_35\">\n                    <h2 class=\"\">\n                        Прохоровка,<br>\n                        Белгородская<br>\n                        область, Россия\n                    </h2>\n                    <p>\n                        Для производства сыра «Альметте» в&nbsp;России был выбран север Белгородской\n                        области&nbsp;— одно из&nbsp;самых экологически чистых мест в&nbsp;стране, где каждый\n                        год производят более 500 тысяч тонн молока. Это сделало возможным осуществлять его прямые\n                        поставки: после дойки молоко охлаждают и&nbsp;привозят с&nbsp;ферм на&nbsp;завод, где после\n                        необходимой проверки оно поступает в&nbsp;производство.\n                    </p>\n                </article>\n                <div class=\"close\" (click)=\"geographyPopupClose()\">\n                    <svg class=\"close-svg\">\n                        <use xlink:href=\"/assets/img/svg-sprite/svg.svg#ico_close_d\"></use>\n                    </svg>\n                </div>\n            </div>\n            <div class=\"b-flex_horizontal_end\">\n                <div class=\"geography__photo\">\n                    <img src=\"https://almette.ru/images/geography/06_geography_prokhorovka.jpg\" class=\"image_cover mobile__hide\" alt=\"\">\n                    <img src=\"https://almette.ru/images/mobile/geography/06_geography_prokhorovka_mob.jpg\" class=\"image_cover desktop__hide\" alt=\"\">\n                </div>\n            </div>\n        </div>\n    </div>\n</div>");

