/* harmony default export */ __webpack_exports__["default"] = ("<div class=\"recipes-page\">\n    <section class=\"recipes recipes-1\">\n        <div class=\"wrapper\">\n            <div class=\"box box-middle box_white\" appAnimateTranslate=\"speed_slow\">\n                <article class=\"pos-top pos-top_35\">\n                    <h1 class=\"\">\n                        Отборные<br>\n                        ингредиенты\n                    </h1>\n                    <h5>\n                        Рецепт «Альметте» родился в&nbsp;предгорье Баварских Альп (г.&nbsp;Шонгау), где\n                        приготовление домашнего сыра уже не&nbsp;одно столетие является традиционным.\n                    </h5>\n                    <p>\n                        В&nbsp;основе рецепта&nbsp;— молодой альпийский сыр, взбитый со&nbsp;сливками.\n                    </p>\n                </article>\n            </div>\n            <div data-animate=\"speed_middle\" class=\"recipes-1__photo\" appAnimateTranslate=\"speed_middle\">\n                <img src=\"https://almette.ru/images/recipes/banka.png\" class=\"mobile__hide\" alt=\"\">\n                <img src=\"https://almette.ru/images/mobile/recipes/05_recipy_items_up.png\" class=\"desktop__hide\" alt=\"\">\n            </div>\n        </div>\n    </section>\n    <section class=\"recipes recipes-2\">\n        <div class=\"wrapper b-flex_horizontal_end\">\n            <div class=\"box box-middle box_light-blue\" appAnimateTranslate=\"speed_slow\">\n                <article class=\"pos-top pos-top_35\">\n                    <h6 class=\"\">\n                        Молоко <br>\n                        и&nbsp;сливки\n                    </h6>\n                    <h5>\n                        Творожный сыр «Альметте» можно приготовить только из&nbsp;свежего молока.\n                    </h5>\n                    <p>\n                        Из&nbsp;него делают творог и&nbsp;натуральные сливки&nbsp;— главные ингредиенты молодого сыра.\n                    </p>\n                </article>\n            </div>\n        </div>\n    </section>\n    <section class=\"recipes recipes-3\">\n        <div class=\"wrapper\">\n            <div class=\"box box-middle box_light-gray\" appAnimateTranslate=\"speed_slow\">\n                <article class=\"pos-top pos-top_35\">\n                    <h6 class=\"\">\n                        Нежная  <br>\n                        консистенция\n                    </h6>\n                    <h5>\n                        Сливки для творожного сыра «Альметте» получают путем сепарации молока.\n                    </h5>\n                    <p>\n                        Именно благодаря использованию натуральных ингредиентов и&nbsp;деликатному взбиванию сыра, получается тот самый сливочный вкус и&nbsp;нежная консистенция Альметте, за&nbsp;которые его ценят повара и&nbsp;обычные потребители.\n                    </p>\n                </article>\n            </div>\n        </div>\n    </section>\n    <section class=\"recipes recipes-4\">\n        <div class=\"wrapper b-flex_horizontal_end padding-right_zero\">\n            <div class=\"box box-middle box_light-green\" appAnimateTranslate=\"speed_slow\">\n                <article class=\"pos-top pos-top_35\">\n                    <h6 class=\"\">\n                        Лучшие <br>\n                        вкусы\n                    </h6>\n                    <h5>\n                        В&nbsp;альпийской кухне творожный сыр потребляют в&nbsp;сочетании с&nbsp;разными\n                        ингредиентами: ароматными травами, пикантными свежими и&nbsp;маринованными овощами, грибами.\n                    </h5>\n                    <p>\n                        Наиболее популярные из&nbsp;этих сочетаний нашли свое воплощение в&nbsp;российской линейке\n                        «Альметте»:\n                    </p>\n                    <ul>\n                        <li>\n                            <p> «Альметте» <br class=\"tastes-br\">сливочный</p>\n                        </li>\n                        <li>\n                            <p> «Альметте» <br class=\"tastes-br\">с&nbsp;зеленью</p>\n                        </li>\n                        <li>\n                            <p> «Альметте» <br class=\"tastes-br\">с&nbsp;огурцами и&nbsp;зеленью</p>\n                        </li>\n\n                        <li>\n                            <p> «Альметте» <br class=\"tastes-br\">с&nbsp;белыми грибами</p>\n                        </li>\n                        <li>\n                            <p> «Альметте» <br class=\"tastes-br\">с&nbsp;томатами по-итальянски</p>\n                        </li>\n                        <li>\n                            <p> «Альметте» <br class=\"tastes-br\">с&nbsp;чесноком</p>\n                        </li>\n\n                        <li>\n                            <p> «Альметте» <br class=\"tastes-br\">с&nbsp;сыром Маасдам</p>\n                        </li>\n                    </ul>\n                </article>\n            </div>\n            <div class=\"recipes-4__photo\" appAnimateTranslate=\"speed_fast\">\n                <img src=\"https://almette.ru/images/recipes/buterbrod.png\" class=\"\" alt=\"\">\n            </div>\n        </div>\n    </section>\n    <section class=\"recipes recipes-5\">\n        <div class=\"wrapper\">\n            <div class=\"recipes__circle recipes__circle__left\">\n                <div class=\"recipes__circle-photo\">\n                    <img src=\"https://almette.ru/images/svg-sprite/ill_1.svg\" alt=\"\">\n                </div>\n                <div class=\"recipes__title js__test\" appAnimateTranslate=\"speed_fast\">\n                    <h3>\n                        Без консервантов\n                    </h3>\n                    <p>\n                        Как и&nbsp;много лет назад в&nbsp;рецепте по-прежнему не&nbsp;используют консерванты. Нежная\n                        консистенция сыра получается благодаря деликатному взбиванию со&nbsp;сливками, а&nbsp;срок\n                        годности 120 суток достигается за&nbsp;счет термизации в&nbsp;процессе производства и&nbsp;строгого\n                        контроля микробиологических показателей.\n                    </p>\n                </div>\n            </div>\n        </div>\n    </section>\n\n    <section class=\"recipes recipes-6\">\n        <div class=\"wrapper b-flex_horizontal_end\">\n            <div class=\"recipes__circle recipes__circle__right\">\n                <div class=\"recipes__circle-photo\">\n                    <img src=\"https://almette.ru/images/svg-sprite/ill_2.svg\" alt=\"\">\n\n                </div>\n                <div class=\"recipes__title\" appAnimateTranslate=\"speed_fast\">\n                    <h3>\n                        Входной контроль <br> <br class=\"tastes-br\">\n                        качества\n                    </h3>\n                    <p>\n                        При поступлении на&nbsp;завод молоко попадает в&nbsp;специальную лабораторию, где проводится\n                        проверка на&nbsp;безопасность и&nbsp;качество. Только после прохождения всех необходимых\n                        тестов, молоко идет в&nbsp;<a href=\"/production\" class=\"color_red\">производство</a>.\n                    </p>\n                </div>\n            </div>\n        </div>\n    </section>\n</div>");

